--secondary-color: #60778d;\r\n}\r\n\r\nbody,\r\nhtml,\r\n#app {\r\n  min-height: 100vh !important;\r\n  height: auto !important;\r\n  overflow-x: hidden !important;\r\n  overflow-y: auto !important;\r\n}\r\n\r\nbody {\r\n  font-family: \"Segoe UI\", Tahoma, Geneva, Verdana, sans-serif;\r\n  background-color: var(--dark-bg);\r\n  color: var(--text-color);\r\n  margin: 0;\r\n  padding: 0;\r\n  overflow-x: hidden;\r\n}\r\n\r\n.container {\r\n  max-width: 1200px;\r\n  margin: 0 auto;\r\n  padding: 0 20px;\r\n}\r\n\r\n.navbar {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  background: #1e2a3a;\r\n  padding: 15px 40px;\r\n  margin: 0;\r\n  box-shadow: none;\r\n  border-radius: 0;\r\n}\r\n\r\n.logo {\r\n  display: flex;\r\n  align-items: center;\r\n  font-weight: bold;\r\n  font-size: 1.4rem;\r\n  color: #fff;\r\n}\r\n\r\n.logo-icon {\r\n  font-size: 1.8rem;\r\n  margin-right: 10px;\r\n  color: var(--primary-color);\r\n}\r\n\r\n.nav-links {\r\n  display: flex;\r\n  gap: 40px;\r\n  list-style: none;\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\n.nav-links li {\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n  transition: color 0.2s;\r\n  font-size: 1rem;\r\n}\r\n\r\n.nav-links li a {\r\n  color: white;\r\n}\r\n\r\n.nav-links li:hover,\r\n.nav-links li.active {\r\n  color: var(--primary-color);\r\n}\r\n\r\n.auth-buttons {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n}\r\n\r\n.register-btn {\r\n  background: transparent;\r\n  color: white;\r\n  border: 1px solid #8a9ba8;\r\n  border-radius: 6px;\r\n  padding: 8px 20px;\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n  font-size: 0.95rem;\r\n  transition: all 0.2s;\r\n}\r\n\r\n.login-btn {\r\n  background: var(--primary-color);\r\n  color: #fff;\r\n  border: none;\r\n  border-radius: 6px;\r\n  padding: 8px 20px;\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n  font-size: 0.95rem;\r\n  transition: background 0.2s;\r\n}\r\n\r\n.register-btn:hover {\r\n  background: #8a9ba8;\r\n  color: white;\r\n}\r\n\r\n.login-btn:hover {\r\n  background: #1e4e7a;\r\n}\r\n\r\n.hero {\r\n  background: linear-gradient(135deg, #4f46e5 0%, #7c3aed 50%, #ec4899 100%);\r\n  color: #fff;\r\n  border-radius: 0;\r\n  padding: 80px 40px;\r\n  text-align: center;\r\n  margin: 0;\r\n  box-shadow: none;\r\n}\r\n\r\n.hero h1 {\r\n  font-size: 3.5rem;\r\n  font-weight: 700;\r\n  margin-bottom: 20px;\r\n  line-height: 1.1;\r\n}\r\n\r\n.hero p {\r\n  font-size: 1.3rem;\r\n  margin-bottom: 40px;\r\n  opacity: 0.9;\r\n  max-width: 600px;\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n}\r\n\r\n.search-bar {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 0;\r\n  max-width: 600px;\r\n  margin: 0 auto;\r\n}\r\n\r\n.search-bar input {\r\n  flex: 1;\r\n  padding: 18px 25px;\r\n  border-radius: 50px;\r\n  border: none;\r\n  background: rgba(255, 255, 255, 0.15);\r\n  color: white;\r\n  font-size: 1.1rem;\r\n  outline: none;\r\n  backdrop-filter: blur(10px);\r\n}\r\n\r\n.search-bar input::placeholder {\r\n  color: rgba(255, 255, 255, 0.7);\r\n}\r\n\r\n.search-btn {\r\n  margin-left: 5px;\r\n  padding: 10px 12px;\r\n  border-radius: 50px;\r\n  border: none;\r\n  background: rgba(255, 255, 255, 0.2);\r\n  color: white;\r\n  font-size: 1.2rem;\r\n  cursor: pointer;\r\n  transition: background 0.2s;\r\n}\r\n\r\n.search-btn:hover {\r\n  background: rgba(255, 255, 255, 0.3);\r\n}\r\n\r\n.filter-bar {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  background: #2a3441;\r\n  padding: 20px 40px;\r\n  margin: 0;\r\n  box-shadow: none;\r\n  border-radius: 0;\r\n}\r\n\r\n.filter-bar span {\r\n  font-size: 1rem;\r\n  font-weight: 600;\r\n  color: #fff;\r\n}\r\n\r\n.genre-filter,\r\n.platform-filter {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 20px;\r\n}\r\n\r\n.genre-list {\r\n  display: flex;\r\n  gap: 8px;\r\n}\r\n\r\n.genre-btn {\r\n  background: #3a4553;\r\n  color: #8a9ba8;\r\n  bor