___CSS_LOADER_EXPORT___.push([module.id, `.rekomendasi-page {
  text-align: center;
  margin-bottom: 30px;
}

.rekomendasi-page h1 {
  font-size: 2.5rem;
  font-weight: 700;
}

.rekomendasi-page p {
  font-size: 1rem;
  color: #d1d1d1;
  max-width: 600px;
  margin: 0 auto;
}

.rekomendasi-form {
  background: rgba(255, 255, 255, 0.05);
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  margin-bottom: 40px;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.form-group-rec {
  flex: 1;
  margin-bottom: 0;
}

.form-group-rec h2 {
  font-size: 1rem;
  margin-bottom: 8px;
  color: #c5c5c5;
}

.form-group-rec input[type="text"],
.form-group-rec input[type="number"] {
  width: 100%;
  padding: 12px 15px;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  outline: none;
  background: #1e1e2f;
  color: #fff;
  box-shadow: inset 0 0 5px rgba(255, 255, 255, 0.05);
  box-sizing: border-box;
}

.parse-btn {
  display: block;
  width: 100%;
  padding: 12px 15px;
  font-size: 1rem;
  font-weight: bold;
  background: linear-gradient(90deg, #7b2ff7, #f107a3);
  border: none;
  border-radius: 8px;
  color: #fff;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  box-sizing: border-box;
  margin-top: 15px;
}

.parse-btn:hover {
  transform: scale(1.03);
  box-shadow: 0 5px 15px rgba(255, 0, 153, 0.4);
}
